* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;

}
/* var(--color-bg) this is a css variable for our backgroundcolor so that we wont be typing the css bg-color names everytime. */ 


body {

    background: var(--color-bg);  

}
.gradient__bg { 

    /* ff 3.6+ */

    /* safari 5.1+,chrome 10+ */

    /* opera 11.10+ */

    /* ie 10+ */

    /* global 92%+ browsers support */
    background: radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(0, 40, 83, 1) 100%, rgba(4, 12, 24, 1) 100%); 

}

.gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; 

}
.section__padding {
    padding: 4rem 6rem;
}

.section__margin {
    margin: 4rem 6rem; 
}

.scale-down-center {
    -webkit-animation: scale-down-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation: scale-down-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}


@-webkit-keyframes scale-down-center { 
    0% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }

    100% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }
}

@keyframes scale-down-center {
    0% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }

    100% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }
}

/* Media queries for tablets or bigger phones. */
@media screen and (max-width: 700px) {
    .section__padding {
        padding: 4rem;
    }
    .section__margin {
        margin: 4rem;
    }
}
/* Media Queries for mobile devices. */ 
@media screen and (max-width: 550px) { 
    .section__padding {
        padding: 4rem 2rem;  
    } 

    .section__margin {
        margin: 4rem 2rem;  
    }
}
/* SOCIAL STUFF */
.social-container {
    background: #eee;  
    padding: 25px 50px;
    display: flex;
    justify-content: center; 
}

a.social {
    margin: 0 1rem;
    transition: -webkit-transform 250ms;
    transition: transform 250ms;
    transition: transform 250ms, -webkit-transform 250ms; 
    display: inline-block;
}

a.social:hover {
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
}

a.youtube {
    color: #eb3223;
}

a.facebook {
    color: #4968ad;
}

a.twitter {
    color: #49a1eb;
}

a.instagram {
    color: black;
}
:root {
    --font-family: 'Manrope', sans-serif;

    --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
    --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);

    --color-bg: #040C18;
    --color-footer: #031B34;
    --color-blog: #042c54;
    --color-text: #81AFDD;
    --color-subtext: #FF8A71; 
}
 
.contact {
    width: 100%;
    height: 100vh;
    position: relative;
    background: rgba(0, 0, 0, .5);
}

.contact .container .form-container h1 {
    padding-top: 70px; 
}

.contact:before {
    content: '';
    position: absolute;
    background: url(/static/media/contact-bg.c2dab756.png) no-repeat center center/cover;  
    width: 100%;
    height: 100%;
    z-index: -1;

}

.contact .container .form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: #f8f8f8;

}

.contact form {
    width: 50%;
    margin: 1rem;

}

.contact .form-container div {
    display: flex;
    flex-direction: column;

}

.contact form label {
    margin: 2rem 0 .7rem 0;
    display: none;
}

.contact form input,
textarea {
    padding: .5rem;
    font-size: 1.2rem;
    color: #f8f8f8;
    margin: 1rem 0;
    font-family: 'Roboto', sans-serif;
    background: rgba(0, 0, 0, .15);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    -webkit-backdrop-filter: blur(4px);
            backdrop-filter: blur(4px);
    border-radius: 4px;
    border: 1px solid rgba(0, 0, 0, .15)
}

.contact form input:focus,
textarea:focus {
    outline: var(--primary-color);
    box-shadow: 0 0 12px 0 var(--primary-color);

}

@media screen and (max-width:940px) {
    .contact form {
        width: 100%;

    }
}
